import { MODAL_ON, MODAL_OFF } from './Actions'
import $ from 'jquery'
import authReducer from './AuthReducer'

function errorReducer(state, action) {
  switch (action.type) {
    case 'AUTH_ERROR':
      return 'Failed to authenticate'
    default:
      return state
  }
}

function layoutReducer(state, action) {
  switch (action.type) {
    case MODAL_ON:
      $("#" + action.component).modal("show");
      return state
    case MODAL_OFF:
      $("#" + action.component).modal("hide");
      return state
    default:
      return state

  }
}

// application reducer: receives state and action and returns
// the a single global state object
// reducer Y must return state for action taken by reducer X
export default function appReducer(state, action) {
  return {
    auth: authReducer(state.auth, action),
    error: errorReducer(state.error, action),
    layout: layoutReducer(state.error, action)
  }
}

