// React and Hooks
import React, { useEffect, useReducer } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { useResource } from 'react-request-hook';

// components
import HorizontalNav from "./layout/HorizontalNav"
import AuthModal from "./user/AuthModal"
import Profile from "./user/Profile";
import Search from "./coach/Search";
import CoachProfile from './coach/Profile'

// pages
import Home from "./layout/Home";
import About from "./layout/About";
import FAQ from "./layout/FAQ";

// contexts && reducers
import { Context } from './Contexts'
import appReducer from './reducers'

const App = () => {

  // initialize the global reducer
  const [state, dispatch] = useReducer(appReducer,
    {
      auth: '',
      error: '',
      layout: { modal: { display: false } }
    });

  // login with token if token exists
  const [user, authenticate] = useResource(data =>
    ({
      url: "/api/v1/users/login",
      method: "post",
      data: data
    }));
  useEffect(() => {
    if (state.auth.isAuthenticated) { return; }
    const token = localStorage.token;
    if (token && token !== "") {
      authenticate({ token: token });
    }
  }, [state, authenticate]);

  useEffect(() => {
    if (user && user.data && !user.error) dispatch({ type: 'LOGIN', data: user.data });
  }, [user])

  return (
    <Context.Provider value={{ state, dispatch }}>
      <div className="container">
        <Router>
          <HorizontalNav />
          <AuthModal />
          <div className="mt-5 pt-2"></div>
          <Switch>
            <Route exact path="/" component={Home} />
            <Route exact path="/about" component={About} />
            <Route exact path="/faq" component={FAQ} />
            <Route exact path="/coaches" component={Search} />
            <Route exact path="/profile" component={Profile} />
            <Route exact path="/coaches/:id" component={CoachProfile} />
          </Switch>
          <div className="mt-5" id="footer"></div>
        </Router>
      </div>
    </Context.Provider>
  );
}

export default App;
