import React, { useState, useEffect } from 'react';
import { useResource } from 'react-request-hook';
import { Link } from 'react-router-dom';
import SearchFilters from './SearchFilters'
import ListingItem from './ListingItem'

const Search = () => {

  // filters has 2 states: {} (empty) --- OR ---
  // { text: [...], gender: [...], position: [...]} (with all filters as arrays)
  // Example: {  text: ["example text"], gender: ["male"," female"], position: ["Mid", "Att", "Def"] }
  const [filters, setFilters] = useState({});
  const [listing, setListing] = useState([]);

  // const total = Object.values(add).reduce((t, {value}) => t + value, 0)
  const filterQuerify = (filters) => {
    return Object.keys(filters).reduce((final, filterItem) => {
      if (filters[filterItem] && filters[filterItem].length) {
        final = final + filterItem + "=" + filters[filterItem].join() + "&"
      }
      return final
    }, "?")
  };

  const [coaches,] = useResource(() => (
    {
      url: `/api/v1/coaches${filterQuerify(filters)}`,
      method: "get"
    }
  ), [filters]);

  useEffect(() => coaches.data && setListing(coaches.data.coaches), [coaches])

  const removeFilter = (e) => {
    let newFilters = {}
    const value = e.target.getAttribute("id")
    const type = e.target.getAttribute("name")
    newFilters[type] = filters[type] && filters[type].filter(f => f !== value)
    setFilters({ ...filters, ...newFilters })
  }

  const hasActiveFilter = () => {
    const reducer = (status, filter) => status && (filter === undefined || filter.length === 0);
    return !Object.values(filters).reduce(reducer, true);
  }

  // Grid layout: content must be placed within columns and only columns may be immediate children of rows.
  return (
    <div id='search' className="">
      <SearchFilters setFilters={setFilters} filters={filters} />

      <div className="row mt-4 justify-content-between">
        <h3 className="col">Coaches</h3>
        <button className="mx-3" type="button" data-toggle="modal" data-target="#coachFilterModal">
          <i className="fas fa-sliders-h" /> Filters
        </button>
      </div>

      {hasActiveFilter() &&
        <div className="row">
          <div className="col mx-auto" id="activeFilters">
            {Object.entries(filters).map(filter => {
              return (filter[1] === undefined || filter[1].length === 0) ? "" :
                filter[1].map(f => < div className="border p-2 mr-1 btn btn-light" id={f} key={f} name={filter[0]} onClick={e => removeFilter(e)}>{f} <i className="fas fa-times" /></div>)
            })}
          </div>

        </div>}

      <div className="row mx-auto">
        <div className="col-12" id="listing">
          {!listing.length &&
            <div><p className="text-center font-weight-bold text-dark mt-4">No Results Found</p>
              <p className="text-center">None of our coaches match these criteria.
                Try broadening the search criteria and <Link to="#" className="font-weight-bold text-dark" data-toggle="modal" data-target="#coachFilterModal">searching again.</Link>
              </p>
            </div>
          }
          <div className="row">
            {listing.map(coach => <ListingItem coach={coach} key={coach._id} />)}
          </div>
        </div>
      </div>
    </div >
  )
}

export default Search;