import React, { useState, useEffect, useContext } from 'react';
import { Context } from '../Contexts'
import { useResource } from 'react-request-hook';
import classnames from 'classnames';
import { Link } from 'react-router-dom';

export default function AuthModal() {

  const initialState = { name: '', email: '', password: '', token: '', formRegister: true }
  const [data, setData] = useState(initialState);
  const { dispatch } = useContext(Context);
  const [user, authenticate] = useResource(data => {
    return data.formRegister ?
      {
        url: "/api/v1/users",
        method: "post",
        data: data
      } : {
        url: "/api/v1/users/login",
        method: "post",
        data: data
      }
  });

  useEffect(() => {
    if (user && user.data && !user.error) {
      dispatch({ type: data.formRegister ? 'REGISTER' : 'LOGIN', data: user.data });
      setData(initialState);
      dispatch({ type: 'MODAL_OFF', component: "authModal" });
    }
    if (user && user.error) {
    }
  }, [user])

  return (
    <div className="modal fade" id="authModal" tabIndex="-1" role="dialog">
      <div className="modal-dialog modal-dialog-centered" role="document">
        <div className="modal-content">
          <div>
            <div className="modal-body">
              <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
              <div>
                <h4 className="title">{data.formRegister ? "Register" : "Sign-in"}</h4>
                <Link to="#" className="btn" onClick={() => setData({ ...data, formRegister: !data.formRegister })}>
                  {data.formRegister ? "(Sign-in)" : "(Register)"}
                </Link>
                <div className="mt-4">
                  <form onSubmit={e => { e.preventDefault(); authenticate(data); }}>
                    {data.formRegister &&
                      <div className="form-group">
                        <label htmlFor="name">Name</label>
                        <input
                          type="name"
                          name="name"
                          className={classnames('form-control form-control-lg', { 'is-invalid': false })}
                          value={data.name}
                          onChange={e => setData({ ...data, [e.target.name]: e.target.value })}
                        />
                      </div>}
                    <div className="form-group">
                      <label htmlFor="email">Email</label>
                      <input
                        type="email"
                        name="email"
                        className={classnames('form-control form-control-lg', { 'is-invalid': false })}
                        value={data.email}
                        onChange={e => setData({ ...data, [e.target.name]: e.target.value })}
                      />
                    </div>
                    <div className="form-group">
                      <label htmlFor="password">Password</label>
                      <input
                        type="password"
                        name="password"
                        className="form-control"
                        value={data.password}
                        onChange={e => setData({ ...data, [e.target.name]: e.target.value })}
                      />
                    </div>
                    <div className="pt-3 text-center">
                      {data.formRegister ?
                        <input type="submit" value="Create account" className="btn btn-primary" />
                        :
                        <input type="submit" value="Sign-in" className="btn btn-primary" />
                      }
                    </div>
                  </form>
                </div>
              </div >

            </div>
          </div>
        </div>
      </div >
    </div>

  )
}