import React from 'react';
import Ratings from './Ratings';
import { Link } from 'react-router-dom';
import { months } from '../util/date';

const sessionListingItems = sessions => {

  return sessions.map(session => {
    const date = new Date(session.agenda.date);
    const day = date.getDate();
    const dateString = `${months[date.getMonth()]}. ${day}${day === 1 ? "st" : day === 2 ? "nd" : day === 3 ? "rd" : "th"}`;
    const time = (session.agenda.startAt > 12) ? (session.agenda.startAt - 12) + "pm" : (session.agenda.startAt - 12) + "am"
    const slots = 3;
    return (
      <Link className="dropdown-item" key={session._id} to="#">
        {`${session.title}: ${dateString} ${time} – ${slots} open slots`}
      </Link>
    )
  });

}

const ListingItem = (props) => {

  const sessions = props.coach.athlete.sessions

  return (
    <div key={props.coach._id} className="card bg-light mx-sm-3 my-3 col-12 col-sm" style={{ cursor: 'pointer' }}>
      <div className="row pt-4 justify-content-center">
        <div className="col-5">
          <img className="rounded w-100" src={props.coach.picture} alt={props.coach.name} />
        </div>
        <div className="col-auto">
          <div className="col-12">
            <span className="font-weight-bold">{props.coach.name}</span>
          </div>
          <Ratings rate={props.coach.rating || 4} />
        </div>
      </div>
      <div className="card-body">
        <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
        <ul className='list-group'>
          <li className='list-group-item'>Location: {props.coach.location}</li>
          <li className='list-group-item'>Email: {props.coach.email}</li>
          <li className='list-group-item'>Phone: {props.coach.phone}</li>
        </ul>
        {sessions.length > 0 ? <div className="w-100 text-center mt-3" id="opensessions">
          <div className="dropdown show">
            <Link className="btn btn-secondary dropdown-toggle" to="#" role="button" id="dropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              {sessions.length} Open session{sessions.length > 1 ? "s" : ""}
            </Link>
            <div className="dropdown-menu" aria-labelledby="dropdownMenuLink">
              {sessionListingItems(sessions)}
            </div>
          </div>
        </div> : <div className="col mt-2"><p>No upcoming sessions scheduled</p></div>}
      </div>
    </div>
  )
}

export default ListingItem