import React, { useState, useEffect, useContext } from 'react';
import { Context } from '../Contexts'
import { useResource } from 'react-request-hook';
import AWS from 'aws-sdk'
import { Link, useHistory } from 'react-router-dom';
import $ from 'jquery';

const config = {
  bucketName: 'train-my-game',
  dirName: 'profile', /* optional */
  region: 'us-east-2',
  accessKeyId: process.env.REACT_APP_AWS_ACCESS_KEY_ID,
  secretAccessKey: process.env.REACT_APP_AWS_SECRET_ACCESS_KEY,
}

console.log(config.accessKeyId, config.secretAccessKey)
AWS.config.update({ region: config.region, accessKeyId: config.accessKeyId, secretAccessKey: config.secretAccessKey });
const s3 = new AWS.S3({ apiVersion: '2006-03-01' });

const Profile = () => {

  const [sessions,] = useState([]);
  const { dispatch } = useContext(Context);
  const { state } = useContext(Context);
  const [profilePic, setProfilePic] = useState(state.auth.user && state.auth.user.picture);
  const [, updateProfile] = useResource(data => (
    {
      url: `/api/v1/users/${state.auth.user._id}`,
      method: "put",
      data: data
    }
  ));

  const uploadFile = (file) => {
    const uploadParams = {
      ACL: "public-read",
      Bucket: config.bucketName,
      Key: `avatar/${user._id}-${Date.now()}`,
      Body: file
    };

    s3.upload(uploadParams, function (err, data) {
      if (err) {
        console.log("Error", err);
      } else {
        const newUrl = `https://train-my-game.s3.us-east-2.amazonaws.com/${data.key}`;
        updateProfile({ picture: newUrl });
        dispatch({ type: 'UPDATE_PROFILE_PICTURE', url: newUrl });
      }
    })
  }

  const history = useHistory();

  useEffect(() => {
    !state.auth.user && history.push('/')
  }, [state, history])

  useEffect(() => {
    state.auth.user &&
      setProfilePic(state.auth.user.picture)
  }, [state])

  const calculateAge = dob => {
    var diff_ms = Date.now() - (new Date(dob)).getTime();
    var age_dt = new Date(diff_ms);

    return Math.abs(age_dt.getUTCFullYear() - 1970);
  }

  const user = state.auth.user || ""
  return (
    < div id='profile' >
      <div className="row my-4 mx-2">
        <div className="col-12 mb-5 py-4 border">
          <h3 className="">Profile</h3>

          <div className="row">
            <div className="col-12 col-md-6" id="profile-picture">
              <div className="row bg-light border rounded m-1 p-2">
                <div className="col-6">
                  <img className="rounded mx-auto w-100" id="profile_picture" src={profilePic} alt="avatar" />
                  <Link
                    to="#"
                    style={{ position: "absolute", right: "-115px", bottom: "0", zIndex: "100" }}
                    onClick={() => $("#imageUpload").click()}>
                    Upload picture
                  </Link>
                  <input id="imageUpload" style={{ display: "none" }} type="file" onChange={e => uploadFile(e.target.files[0])} />
                </div>
                <div className="col-6">
                  <div className="row">
                    <div className="col-12 h5">
                      {user && user.name.split(" ")[0]}
                    </div>
                    <div className="col-12 h5">
                      {user && user.name.split(" ").length > 1 && user.name.split(" ")[1]}
                    </div>
                  </div>
                </div>

              </div>
            </div>

            <div className="col-12 col-md-6 mt-3 mt-md-0" id="user_info">
              <p className="h6 font-weight-bold">Bio</p>
              <div className="bg-light border rounded m-1 p-2">
                <div className="row">
                  {user.dob && <div className="col-12 col-md-6">Age: {calculateAge(user.dob)} ({(new Date(user.dob)).toLocaleDateString()})</div>}
                  {user.athlete && <div className="col-12 col-md-6">Position: {user ? user.athlete.position.join(", ") : ""}</div>}
                  {user.phone && <div className="col-12 col-md-6">Contact: user.phone</div>}
                </div>
                <div className="row">
                  <div className="col">
                    <p className="h6 font-weight-underline mt-3"><u>Personal</u></p>
                    <p className="text-justify">{user && user.bio ?
                      user.bio : "You’ll get practical tips on generating lorem ipsum using popular design and development software tools like Visual Studio and Adobe Illustrator. And we’ll share some funny lorem ipsum generators created for entertainment purposes."}</p>
                  </div>
                </div>
              </div>
            </div>

            {sessions.length ?
              <div className="col-12 mt-3 mt-md-0" id="user_info">
                <p className="h6 font-weight-bold">Upcoming sessions</p>
                <div className="bg-light border rounded m-1 p-2">
                  <div className="row">
                    <div className="col-12 col-sm-3"></div>
                    <div className="col-12 col-sm-3"></div>
                    <div className="col-12 col-sm-3"></div>
                    <div className="col-12 col-sm-3"></div>
                  </div>
                </div>
              </div> : ""}

          </div>

        </div>
      </div>
    </div >
  )
}

export default Profile;